.cardContainer {
  padding-top: 10px;
}

.goBackBtn {
  border: 0;
  padding: 5px;
  background-color: #e9ebec;
  cursor: pointer;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-transform: uppercase;
  margin-bottom: 10px;
  border: 1px solid #3535ac;
  border-radius: 5px;
}

.goBackBtn:hover,
.goBackBtn:focus {
  color: white;
  background-color: #3535ac;
}

.card {
  display: flex;
  gap: 25px;
  width: 70%;
}

.cardInfo {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.movieTitle {
  font-size: 36px;
}

.score {
  font-size: large;
}

.overviewTitle {
  font-size: 26px;
  font-weight: 500;
}

.genresTitle {
  font-size: 22px;
  font-weight: 500;
}

.genresText {
  text-transform: uppercase;
  margin-top: -3px;
}

.addInfo {
  padding-top: 10px;
  padding-left: 10px;
  border-bottom: 1px solid #3535ac;
  margin-bottom: 15px;
}

.addTitle {
  text-transform: uppercase;
  font-weight: 400;
}

.addList {
  padding: 15px 0;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.addLink {
  font-weight: 500;
}
